<template>
  <div id="app show-modal">
<p> Sīkāka informācija sekos</p>
  </div>
</template>

<script>
  import Nav from "./components/Nav";

  export default {
    components: {Nav},
    name: 'App'
  }
</script>
<style lang="css">
  @import '/styles/app/foundation.min.css';
</style>
<style>
  body {
    background-color: #010101;
    background-image: url("./assets/poster12.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: auto 100%;
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  }

  .container-grid {
    padding-left: 5%;
    padding-top: 0;
    padding-bottom: 2%;
    padding-right: 25%;
  }

  .button-squared {
    white-space: normal;
    margin: 10px;
    width: 300px !important;
    height: 300px !important;
    background-color: #E3E3E3;
  }
  .image {
    float: right;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    border: 1px solid #ebebeb;
    margin: 5px;
    display: block;
  }
  .inline > li {
    display: inline-block;
  }

  .button-squared > a {
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding-bottom: 50%;

  }

  a:hover {
    text-decoration: none;
  }

  nav {
    display: inline-block;
    margin: 60px auto 45px;
    background-color: #172636;
    box-shadow: 0 1px 1px #ccc;
    border-radius: 2px;
  }

  nav li {
    display: inline-block;
  }

  nav a {
    display: inline-block;
    padding: 18px 30px;
    color: #fefefe !important;
    font-weight: bold;
    font-size: 16px;
    text-decoration: none !important;
    line-height: 1;
    text-transform: uppercase;
    background-color: transparent;

    -webkit-transition: background-color 0.25s;
    -moz-transition: background-color 0.25s;
    transition: background-color 0.25s;
  }

  nav a:first-child {
    border-radius: 2px 0 0 2px;
  }

  nav a:last-child {
    border-radius: 0 2px 2px 0;
  }

  nav.home .home,
  nav.performances .performances,
  nav.artists .artists,
  nav.support .support,
  nav.workshops .workshops,
  nav.ananda .performances {
    background-color: #EF3D83;
  }

  p {
    font-size: 22px;
    font-weight: bold;
    color: #7d9098;
  }

  p b {
    color: #ffffff;
    display: inline-block;
    padding: 5px 10px;
    background-color: #c4d7e0;
    border-radius: 2px;
    text-transform: uppercase;
    font-size: 18px;
  }

  h3 {
    color: #EF3D83;
  }

  .modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
  }

  .modal-wrapper {
    display: table-cell;
    vertical-align: middle;
  }

  .modal-container {
    width: 70%;
    height: 80%;
    overflow: auto;
    margin: 0 auto;
    padding: 20px 30px;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
  }

  .modal-header h3 {
    margin-top: 0;
    color: #42b983;
  }

  .modal-body {
    margin: 20px 0;
  }

  .modal-default-button {
    float: right;
  }

  /*
   * The following styles are auto-applied to elements with
   * transition="modal" when their visibility is toggled
   * by Vue.js.
   *
   * You can easily play with the modal transition by editing
   * these styles.
   */

  .modal-enter {
    opacity: 0;
  }

  .modal-leave-active {
    opacity: 0;
  }

  .modal-enter .modal-container,
  .modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

</style>



