<template>
  <div>
  <div v-if="grid" class="container-grid">
    <ul class="inline">
      <li v-for="event in grid" :key="event.target">
        <button class="button-squared" @click="loadFromGrid(event.target)"
                :style="{ backgroundImage: 'url('+ imageUrl + event.target + imageEnd +')', backgroundSize: 'cover'}"><a
          href="#" :class="event.target">{{event.title}}</a></button>
      </li>
    </ul>
  </div>
  <div v-else-if="calendar" class="calendar">
    <Accordion :calendar="calendar" />
  </div>
    <Modal v-if="showModal" @close="showModal = false" :body="data.body" :title="data.title" :date="data.date"
           :spot="data.spot" :crew="data.crew" :images="images" :date1="data.date1" :date2="data.date2" :showImages="showImages">
    </Modal>
  </div>
</template>

<script>
  import Modal from "./Modal";
  import Accordion from "./Accordion";

  export default {
    name: "Grid",
    components: {Accordion, Modal},
    props: ['grid', 'active', 'calendar'],
    data: function () {
      return {
        imageUrl: "http://www.laiksdejotfestivals.lv/data/",
        imageEnd: ".png",
        images: null,
        data: null,
        showImages: true,
        showModal: null
      }
    },
    methods: {
      loadFromGrid(target) {
        const url = 'http://www.laiksdejotfestivals.lv/data/';
        fetch(url + target + '.json', {mode: 'cors'}).then((r) => {
          r.json().then((result) => {
            this.data = result;
            this.images = result.images;
            this.showModal = true;
          });
        })
      }
    }
  }
</script>

<style scoped>
  .container-grid {
    padding-bottom: 2%;
  }

  .button-squared {
    white-space: normal;
    margin: 10px;
    width: 200px !important;
    height: 200px !important;
    background-color: #E3E3E3;
    display: flex;
    justify-content: center;
  }

  button > a {
    background-color: #EF3D83;
    color: #e4e4e4;
    font-family: 'Helvetica Neue', sans-serif;
    font-size: 16px;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
    letter-spacing: -1px;
    line-height: 1;
    text-align: center;
    padding:5px;
  }

  .inline > li {
    display: inline-block;
  }

  .button-squared > a {

  }

  a:hover {
    text-decoration: none;
  }

  nav {
    display: inline-block;
    margin: 60px auto 45px;
    background-color: #172636;
    box-shadow: 0 1px 1px #ccc;
    border-radius: 2px;
  }

  nav li {
    display: inline-block;
  }

  nav a {
    display: inline-block;
    padding: 18px 30px;
    color: #fefefe !important;
    font-weight: bold;
    font-size: 16px;
    text-decoration: none !important;
    line-height: 1;
    text-transform: uppercase;
    background-color: transparent;

    -webkit-transition: background-color 0.25s;
    -moz-transition: background-color 0.25s;
    transition: background-color 0.25s;
  }

  nav a:first-child {
    border-radius: 2px 0 0 2px;
  }

  nav a:last-child {
    border-radius: 0 2px 2px 0;
  }

  nav.home .home,
  nav.performances .performances,
  nav.artists .artists,
  nav.support .support,
  nav.workshops .workshops,
  nav.ananda .performances {
    background-color: #EF3D83;
  }

  p {
    font-size: 22px;
    font-weight: bold;
    color: #7d9098;
  }

  p b {
    color: #ffffff;
    display: inline-block;
    padding: 5px 10px;
    background-color: #c4d7e0;
    border-radius: 2px;
    text-transform: uppercase;
    font-size: 18px;
  }

  h3 {
    color: #EF3D83;
  }

  .modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
  }

  .modal-wrapper {
    display: table-cell;
    vertical-align: middle;
  }

  .modal-container {
    width: 70%;
    height: 80%;
    overflow: auto;
    margin: 0 auto;
    padding: 20px 30px;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
  }

  .modal-header h3 {
    margin-top: 0;
    color: #42b983;
  }

  .modal-body {
    margin: 20px 0;
  }

  .modal-default-button {
    float: right;
  }

  /*
   * The following styles are auto-applied to elements with
   * transition="modal" when their visibility is toggled
   * by Vue.js.
   *
   * You can easily play with the modal transition by editing
   * these styles.
   */

  .modal-enter {
    opacity: 0;
  }

  .modal-leave-active {
    opacity: 0;
  }

  .modal-enter .modal-container,
  .modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

</style>



