<template>
  <div>
    <div class="pdf"><a class="pdf-button" href="http://www.laiksdejotfestivals.lv/data/programma.pdf"><span>✓</span>Programma (PDF)</a></div>
    <div>
      <div class="container-grid" v-for="(day, dayIndex) in calendar">
        <div class="header accordion-header">
          {{day.date}}
        </div>
        <div v-for="(event, eventIndex) in day.events" class="my-content">
          <button class="button-squared" @click="loadFromGrid(event.target)"
                  :style="{ backgroundImage: 'url('+ imageUrl + event.target + imageEnd +')', backgroundSize: 'cover'}">
            <a href="#" :class="event.target">{{event.title}} </a></button>
          <div class="event"><br>{{event.chor}} <br>{{event.time}} <br>{{event.venue}}
          </div>
        </div>
      </div>
      <Modal v-if="showModal" @close="showModal = false" :body="data.body" :title="data.title" :date="data.date"
             :spot="data.spot" :crew="data.crew" :images="images" :showImages="showImages">
      </Modal>
    </div>
  </div>
</template>

<script>
  import Modal from "./Modal";

  export default {
    name: "Accordion",
    components: {Modal},
    props: ['calendar'],
    data: function () {
      return {
        imageUrl: "http://www.laiksdejotfestivals.lv/data/",
        imageEnd: ".png",
        data: null,
        images: null,
        showModal: false,
        showImages: true
      }
    },
    methods: {
      loadFromGrid(target) {
        const url = 'http://www.laiksdejotfestivals.lv/data/';
        fetch(url + target + '.json', {mode: 'cors'}).then((r) => {
          r.json().then((result) => {
            this.data = result;
            this.images = result.images;
            this.showModal = true;
          });
        })
      }
    }

  }
</script>

<style scoped>
  .container-grid {
    padding-bottom: 2%;
  }
  .pdf-button {
    background: #EF3D83;
    display: inline-block;
    height: 20px;
    line-height: 20px;
    padding-right: 30px;
    padding-left: 70px;
    position: relative;
    color:rgb(255,255,255);
    text-decoration: none;
    text-transform: uppercase;
    margin-bottom: 15px;


    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    text-shadow:0px 1px 0px rgba(0,0,0,0.5);
    -ms-filter:"progid:DXImageTransform.Microsoft.dropshadow(OffX=0,OffY=1,Color=#ff123852,Positive=true)";zoom:1;
    filter:progid:DXImageTransform.Microsoft.dropshadow(OffX=0,OffY=1,Color=#ff123852,Positive=true);

    -moz-box-shadow:0px 2px 2px rgba(0,0,0,0.2);
    -webkit-box-shadow:0px 2px 2px rgba(0,0,0,0.2);
    box-shadow:0px 2px 2px rgba(0,0,0,0.2);
    -ms-filter:"progid:DXImageTransform.Microsoft.dropshadow(OffX=0,OffY=2,Color=#33000000,Positive=true)";
    filter:progid:DXImageTransform.Microsoft.dropshadow(OffX=0,OffY=2,Color=#33000000,Positive=true);
  }

  .pdf-button span {
    position: absolute;
    text-align: center;
    left: 0;
    width: 25px;
    background-color:rgba(0,0,0,0.5);

    -webkit-border-top-left-radius: 5px;
    -webkit-border-bottom-left-radius: 5px;
    -moz-border-radius-topleft: 5px;
    -moz-border-radius-bottomleft: 5px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border-right: 1px solid  rgba(0,0,0,0.15);
  }

  .pdf-button:hover span, .pdf-button.active span {
    background-color: #42b983;
    border-right: 1px solid  rgba(0,0,0,0.3);
  }

  .accordion-header {
    text-align: center;
    color: #c9d0d4;
    font-family: 'Helvetica Neue', sans-serif;
    font-size: 38px;
    font-weight: 100;
    line-height: 50px;
    letter-spacing: 1px;
    padding: 35px 0 2px;
    border-bottom: double #555;
  }

  .pdf {
    color: #fefefe;
    float: right;
  }

  .event {
    color: #fefefe;
    font-size: small;
    width: 200px;
  }

  .button-squared {
    white-space: normal;
    margin: 10px;
    width: 200px !important;
    height: 200px !important;
    background-color: #E3E3E3;
    display: flex;
    justify-content: center;
  }

  .my-content {
    display: inline-block;
  }

  button > a {
    background-color: #EF3D83;
    color: #e4e4e4;
    font-family: 'Helvetica Neue', sans-serif;
    font-size: 16px;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
    letter-spacing: -1px;
    line-height: 1;
    text-align: center;
    padding: 5px;
  }
</style>



